import { Component } from '@angular/core';

@Component({
    selector: 'nav',
    templateUrl: 'navbar.html',
    styles: [`
    .nav-item.active a{
        color:green;
    }
    .nav-item a{
        color:black;
    }
    .navbar-toggler{
        alight-self:flex-end;
    }
    .nav-item:hover a{
        transition:0.3s;
        transition-timing-function:ease-out;
        color:green;
    }`],
})
    
export class NavbarComponent { }